public poneperfil(): void {
         this.storage.set('username', this.Form.get('email').value.trim());
         this.authService.getReg(this.Form.get('email').value.trim() + '/emailsolo/' +this.APP,'personals').then((data) => {

                  this.storage.set('tiposus', data[0].tiposuscriptor.nombre + ',' + data[0].tiposuscriptor.cobroparqueos+ ',' + data[0].cui+ ',' + data[0]._id+ ',' + data[0].nombre+ ','+data[0].nov+ ','+data[0].role+','+data[0].unidad._id+ ',' + data[0].controlacceso + ','+ data[0].foto);

                  this.events.publish('nombre:cambia', data[0].nombre);
                  this.events.publish('foto:cambia', data[0].foto);
                  this.events.publish('rolx:otro', data[0].role);


                  if(this.APP!='WebApp Usacenlinea')
                  {
                    this.navCtrl.push(MainPage,{uno: "Tab5Root", dos: "Tab5Root" });
                  }
                  else
                  {

                  if(data[0].role=='lluviaideas')
                  {
                    this.navCtrl.push(MainPage,{uno: "Tab4Root", dos: "Tab4Root" });
                  }
                  else
                  {
                    if(this.pag0==0)
                    {
                      this.navCtrl.push(MainPage,{uno: "Tab1Root", dos: "Tab3Root" });
                    }

                  }
                }

              }, (err) => {
                  if(err.error=='NO EXISTE REGISTRO')
                  {
                    this.events.publish('nombre:cambia', '');
                    this.events.publish('foto:cambia', '');
                    this.translateService.get('PROFILE_ERROR').subscribe((value) => {
                      this.msj.showError3('Error',value);
                      })
                    if(this.pag0==0)  {   this.navCtrl.push(MainPage,{uno: "Tab3Root", dos: "Tab1Root" });  }
                  }

              });
}

login(){
        let credentials = {
          idempresa:this.APP,
            email: this.Form.get('email').value,
            password: this.Form.get('password').value,
            bitacora:{idempresa:this.APP,idafiliado:'',email:this.Form.get('email').value,permiso:'Ingreso',accion:'Ingreso al sistema'}
        };

        this.authService.createAPITOKEN(credentials,'/api/auth/login').then((result) => {
        var autt:any=result;
        if(autt.user._id=='-1')
        {
          this.msj.showError3('Precaución',autt.user.role);
        }
        else
        {
          if(autt.user._id=='-11')
          {
            this.msj.showError3('Precaución',autt.user.role);
          }
          else
          {

            if(autt.user.estadoemail=='0')
            {
               this.msj.showError3('Precaución','Por favor ingresa a tu correo y confirma tu suscripción ');

            }
            else
            {
              this.poneperfil();
            }


          }

        }





    }, (err) => {
      this.msj.showError3('Precaución',err.error);
    });
}
