noclave(){


  let prompt = this.alertCtrl.create({
    title:' Ingresa EMAIL y CUI registrado en la plataforma, para poder validar información , Reiniciar clave:',
    inputs: [
      {
        name: 'email',
        placeholder:'Ingrese EMAIL registrado',
        value:this.Form.get('email').value
      },
      {
        name: 'cui',
        placeholder:'Ingrese CUI registrado',
        value:''
      }
    ],
    buttons: [
      {
        text: 'Cancelar'
      },
      {
        text: 'Aceptar',
        handler: todo => {

              if(todo){

         if(todo.email==''  ||  todo.cui=='' )
         {
          this.msj.showError3('Advertencia','Ingrese un CORREO o CUI valido');
          return;

         }


         let credentials = {
           idempresa:this.APP,
          email         	:  todo.email   	,
          cui: todo.cui,
          bitacora:{idempresa:this.APP,idafiliado:'',email: todo.email,permiso:'Reinicia',accion:'Reinicia clave'}
        };

        const loading = this.loadingCtrl.create({
          spinner: 'bubbles',
          content: "Procesando información..."
        });
        loading.present();
        this.authService.createAPISINTOKEN(credentials,'/api/auth/register4').then((result) => {

        var arr:any=[];
       arr=result;
          loading.dismiss();
          var clavexx=arr
          clavexx=clavexx.replace('"','')
          clavexx=clavexx.replace('"','')
          clavexx=clavexx.replace('"','')
          this.msj.showError3('Exito',' Tu nueva contraseña es: ' + clavexx + ' ' );

          this.Form.get('email').setValue(todo.email);
          this.Form.get('password').setValue(clavexx);
          this.login();




        }, (err) => {
          this.msj.showError3('Advertencia',err.error);
          loading.dismiss();

        });





              }


        }
      }
    ]
  });

  prompt.present();

}

